import { FC } from 'react';

import {
  QuoteWrapper,
  StyledSVG,
  StyledTitle,
  StyledQuote,
  StyledCitation,
  StyledImage,
} from './Quote.styles';
import { QuoteProps } from './Quote.types';

/**
 * Quote component for displaying a quote with a title, a citation and an optional image.
 */

const Quote: FC<QuoteProps> = ({ quote, citation, title, image }) => {
  return (
    <QuoteWrapper>
      <StyledSVG />
      <StyledQuote>{quote}</StyledQuote>
      {image && <StyledImage>{image}</StyledImage>}
      <StyledTitle>{title}</StyledTitle>
      <StyledCitation>{citation}</StyledCitation>
    </QuoteWrapper>
  );
};

export default Quote;
